/* main */
.wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100%;
  position: relative;
}
.wrapper::before, .wrapper::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  transition: all 0.3s ease 0s;
}
.wrapper::before {
  background: url("../images/bg-color-yellow.png") 0 0/100% 100% no-repeat;
  opacity: 1;
}
.wrapper::after {
  opacity: 0;
  background: url("../images/bg-color-green.png") 0 0/100% 100% no-repeat;
}
.wrapper.js-active::before {
  opacity: 0;
}
.wrapper.js-active::after {
  opacity: 1;
}
.wrapper .main {
  flex: 1 1 auto;
}

